import { useState, useEffect } from 'react'
import Note from './components/Note/'
import * as noteService from './services/notes.js'
import Notification from './components/Notification/'
import './App.css'

function App() {
  const [ notes, setNotes ] = useState([])
  const [ newNote, setNewNote] = useState('')
  const [ showAll, setShowAll ] = useState(true)
  const [ notification, setNotification ] = useState(null)

  const notesToShow = showAll ? notes : notes.filter(note => note.important)
  const handleNoteChange = (ev) => {
    setNewNote(ev.target.value) 
  }
  const addNote = (ev) => {
    ev.preventDefault()
    const note = {
      content: newNote,
      date: new Date().toISOString(),
      important: Math.random() > 0.5
    }
    noteService
      .create(note)
      .then(data => {
        setNotification({ status: 'success', message: 'note added successfuly'})
        setTimeout(() => {
          setNotification(null)
        }, 5000)
        setNotes([...notes, data ])
      })
    setNewNote('')
  }

  const handleShowAll = (ev) => {
    setShowAll(ev.target.checked)
  }

  const handleImportantChange = (id) => {
    const note = notes.find(note => note._id === id)
    console.log(note);
    const changedNote = { ...note, important: !note.important }
    noteService
      .update(id, changedNote)
      .then(data => {
        setNotes(notes.map(n => n._id !== id ? n : data))
      })
      .catch(error => {
        setNotification({ status: 'error', message: error.message })
        setTimeout(() => {
          setNotification(null)
        }, 5000)
        setNotes(notes.filter(n => n._id !== id))
      })
  }

  const handleDelete = (id) => {
    noteService
      .remove(id)
      .then(data => {
        setNotes(notes.filter(note => note._id !== id))
      })
      .catch(error => {
        setNotification({ status: 'error', message: error.message })
        setTimeout(() => {
          console.log(error);
          setNotification(null)
        }, 5000)
        setNotes(notes.filter(note => note._id !== id))
      })
  }

  useEffect(() => {
     noteService
      .getAll()
      .then(data => {
        setNotes(data)
      })
  }, [])

  return (
    <div>
      <h4>Notes</h4>
      <Notification notification={notification} />
      <form onSubmit={addNote} className='newNote-form'>
        <input className='input-newNote' value={newNote} onChange={handleNoteChange} />
        <button className='btn-newNote' type='submit'>add</button>
      </form>
      <label name='showAll'>
        <input type='checkbox' checked={showAll} onChange={handleShowAll}/>
        showAll
      </label>
      <ul>
        { notesToShow.map(note => 
           <Note 
              key={note._id} 
              note={note} 
              handleImportantChange={handleImportantChange} 
              handleDelete={handleDelete}
          />
        )}
      </ul>
    </div>
  );
}

export default App;
