import './style.css'
import { useState } from 'react'
const Note = ({ note, handleImportantChange, handleDelete }) => {
  const [ imp, setImp ] = useState(note.important)

  const handleChange = (ev) => {
    setImp(ev.target.checked)
    
    handleImportantChange(note._id)
  }

  return (
    <li className='lists'>
      <p>{note.content}</p>
      <input 
        type='checkbox' 
        checked={imp} 
        onChange={handleChange} 
      />
      <button className='btn-del' onClick={() => handleDelete(note._id)}>del</button>
    </li>
  );
};

export default Note
